<template>
  <div class="header">
    <div class="header-title">University管理系统</div>
    <div class="header-right" @click="singOut">
      <i class="fa fa-sign-out"></i>退出登录
    </div>
    <el-image style="header_image" fit="cover" src="../../../static/header.jpg"></el-image>
  </div>
</template>

<script>
export default {
  name:"Vhead",
  methods:{
    singOut(){
      //禁止从登录页重新返回之前浏览的页面
      this.$router.replace("/login")
    }
  }
}
</script>

<style lang="less">
.header{
  position: fixed;
  box-sizing: border-box;
  left: 0;
  top: 0;
  width:100%;
  height: 60px;
  background-color:#002140;
  z-index: 1024;
  .header-title{
    color: #fff;
    font-size: 18px;
    z-index: 1024;
    position: absolute;
    top: 50%;
    left: 30px;
    transform:translateY(-50%)
  }
  .header-right{
    position:absolute;
    top: 50%;
    right: 50px;
    transform:translateY(-50%);
    z-index: 1023;
    color: #fff;
    cursor: pointer;
    i{
      margin-right: 5px;
    }
  }
  .el-image{
    width:100%;
    height:60px;
  }
}
</style>



