<template>
  <div class="DemoSystem">
    <!-- head -->
    <Vhead></Vhead>
    <!-- workArea -->
    <div class="workArea">
      <Vmenu></Vmenu>
      <div class="content">
        <transition name="move" mode="out-in">
          <keep-alive :include="keepAlives">
            <router-view></router-view>
          </keep-alive>
        </transition>
      </div>
    </div>
  </div>
</template>

<script>
import Vhead from "./head"
import Vmenu from "./menu"
export default {
  name:"Layout",
  components:{
    Vhead,
    Vmenu
  },
  data(){
    return{
      keepAlives:this.$store.state.keepAlive  //使用vuex对要缓存的组件进行管理  keepAlives数组存储组件的name,vue会自动匹配此数组中的name以达到缓存对应组件的目的。
    }
  },
  computed:{

  }
}
</script>

<style lang="less">
body,html{//解决顶部导航吸顶时，在内容超出body高度后，导航右侧出现滚动条的问题(让滚动条只出现在导航栏以下区域)
  height: 100%;
  overflow-x: hidden;
}
.DemoSystem {
  overflow-y: hidden;
}

/*定义滚动条高宽及背景
 高宽分别对应横竖滚动条的尺寸*/
::-webkit-scrollbar{
  width:8px;
  // height:8px;
  background-color:#F5F5F5;
}
/*定义滚动条轨道
 内阴影+圆角*/
::-webkit-scrollbar-track{
  -webkit-box-shadow:inset 0 0 6px#F5F5F5;//兼容
  box-shadow:inset 0 0 6px#F5F5F5;
  border-radius:10px;
  background-color:#F0F0F0;
}
/*定义滑块
 内阴影+圆角*/
::-webkit-scrollbar-thumb{
  border-radius:10px;
  -webkit-box-shadow:inset 0 0 6px rgba(192,192,192,0.3);//兼容
  box-shadow:inset 0 0 6px rgba(192,192,192,0.3);
  background-color:#AAAAAA;
}
.workArea{
  height: calc(100vh - 60px);
  margin-top: 60px;
  .content{
    width:calc(100% - 200px);
    margin-left: 200px;
    height: 100%;
  }
}
</style>



