<template>
  <div class="vmenu">
    <el-menu
      class="sidebar-el-menu"
      text-color="#fff"
      active-text-color="#fff"
      background-color="#001529"
      unique-opened
      router>
      <template v-for="item in items">
        <template v-if="item.subs">
          <el-submenu :index="item.index" :key="item.index">
            <template slot="title">
              <em :class="item.icon" style="color:white"></em>
              <span slot="title">{{ item.title }}</span>
            </template>
            <template v-for="subItem in item.subs">
              <el-submenu
                v-if="subItem.subs"
                :index="subItem.index"
                :key="subItem.index">
                <template slot="title">
                  <span>{{ subItem.title }}</span>
                </template>
                <el-menu-item
                  v-for="(threeItem,i) in subItem.subs"
                  :key="i"
                  :index="threeItem.index"
                >{{ threeItem.title }}</el-menu-item>
              </el-submenu>
              <!-- 无二级菜单 显示二级项 -->
              <template v-else>
                <el-menu-item
                  :index="subItem.index"
                  :key="subItem.index">
                  {{ subItem.title }}
                </el-menu-item>
              </template>
            </template>
          </el-submenu>
        </template>
        <template v-else>
          <el-menu-item :index="item.index" :key="item.index">
            <em :class="item.icon"></em>
            <span slot="title">{{ item.title }}</span>
          </el-menu-item>
        </template>
      </template>
    </el-menu>
  </div>
</template>

<script>
import { menus } from "@/data/subItem"
export default {
  name:"Vmenu",
  data(){
    return{
      items:menus
    }
  }
}
</script>

<style lang="less">
.vmenu{
  display: block;
  width:200px;
  height: 100%;
  position: fixed;
  left: 0;
  top: 60px;
  background-color: #001529;
  /* overflow-y: auto;
  overflow-x: hidden; */
  // .el-submenu__title,.el-menu-item{
  //   width:220px;
  // }
  .sidebar-el-menu{
    border-right: none;
  }
}
</style>



