export const actions = {
    setNumFn({commit},obj){
        commit("setNum",obj)
    },
    //keep-alve
    setKeepAlive({commit},data){
      commit("KEEP_ALIVE",data)
    },
    //存储token(此为同异步皆可，采用dispath提交；若为同步可不用在actions里定义，直接使用commit提交mutations里的方法即可)
    setToken({commit},data){
      commit("SET_TOKEN",data)
    },
    //清除token
    removeToken({commit},data){
      commit("REMOVE_TOKEN",data)
    }
}



