import Vue from "vue"
export default
  Vue.directive('throttle',{
    inserted(el){
      el.addEventListener('click',()=>{
        let timer,flag
        el.disabled = true  //<el-button /> disabled属性
        if(!flag){
          flag = true
          timer && clearTimeout(timer)
          timer = setTimeout(()=>{
            flag = false
            el.disabled = false
          },2000)
        }
      },false)
    }
  })



