import Vue from 'vue'
import Router from 'vue-router'
import layout from "../components/layout/layout"

Vue.use(Router)

export default new Router({
  //路由跳转后回到顶部
  scrollBehavior(to, from, savedPosition) {
    return {
      x: 0,
      y: 0
    }
  },
  routes: [
    {
      path:"/",
      redirect:{path:"/login"}
    },
    //登录
    {
      path:'/login',
      component: resolve => require(['@/components/views/login/login.vue'],resolve)
    },
    {
      path:"/layout",
      component:layout,
      children:[    //<router-view />出口在layout组件里,所以为layout的嵌套路由
        //轮播中心*************
        // {
        //   path:'/carousel',
        //   component: resolve => require(['@/components/views/playCenter/carousel.vue'],resolve)
        // },
        // {
        //   path:'/weather',
        //   component: resolve => require(['@/components/views/playCenter/weather.vue'],resolve)
        // },
        // {
        //   path:"/vuexDemo",
        //   component: resolve => require(['@/components/views/playCenter/vuexDemo.vue'],resolve)
        // },
        //图表管理*************
        {
          path:"/echart",
          component: resolve => require(['@/components/views/echartsManage/echart.vue'],resolve)
        },
        {
          path:"/temperature",
          component: resolve => require(["@/components/views/echartsManage/eTemperature.vue"],resolve)
        },
        //图文管理**************
        {
          path:"/picture",
          component: resolve => require(["@/components/views/imgTextManage/picture.vue"],resolve)
        },
        {
          path:"/signature",
          component: resolve => require(["@/components/views/imgTextManage/signature.vue"],resolve)
        },
        //学生信息管理**************
        {
          path:"/studentMgr",
          component: resolve => require(["@/components/views/studentManage/studentMgr.vue"],resolve)
        },
        //工作台**************
        {
          path:"/workbench",
          component: resolve => require(["@/components/views/workBench/workbench.vue"],resolve)
        },
        // {
        //   path:"/scrollIntoView",
        //   component: resolve => require(["@/components/views/workBench/scrollIntoView.vue"],resolve)
        // },
        {
          path:"/virtual",
          component: resolve => require(["@/components/views/workBench/virtual.vue"],resolve)
        }
      ]
    }
  ]
})



