import vue from "vue"
const commonFun = {
  //label-value转换
  transfer:(arr,val)=>{
    let label = ''
    if( !Array.isArray(arr) || arr.length === 0){
      return ''
    }else{
      arr.forEach(item=>{
        if(item.value === val){
          label = item.label
        }
      })
      return label
    }
  },

}

//挂载vue原型
Object.keys(commonFun).forEach(el=>{
  vue.prototype[el] = commonFun[el]
})



