export const state = {
    str:"这是Vuex的数据",
    userInfo:["浦东新区","闵行区",'黄浦区','静安区'],
    userName:'',
    count:0,
    totalPrice:0,
    num:100,//
    keepAlive:[],  //keep-alive include缓存  //组件中想要name值可以从路由中获取 this.$route.name(前提是routes里有name) || this.$route.path.slice(1)
    token:localStorage.getItem('token') ? localStorage.getItem('token'):'',   // token令牌
}



