export const mutations = {
    alertBtn(){
        alert("mutations1")
    },
    changeName(state,name){
        state.userName = name;
    },
    changeNum(state,obj){
        state.userName = obj.name;
        setTimeout(function(){
            alert(obj.a)
        },0)//mutation受同步限制，写异步代码虽然也不影响渲染视图，但会使调试程序变得很复杂。
    },
    setNum(state,obj){
       state.num-=obj.a
    },
    //keep-alive缓存
    KEEP_ALIVE:(state,data)=>{
        state.keepAlive = data
    },
    //存储token
    SET_TOKEN(state,data){
      state.token = data
      localStorage.setItem("token",data)
    },
    REMOVE_TOKEN(state,data){
      localStorage.removeItem("token")
    }
}



