import Vue from 'vue'
import App from './App'
import router from './router';
import store from './store/index'
import axios from "axios";
import Qs from 'qs';
import 'font-awesome/css/font-awesome.min.css'
import throttle from "@/utils/directive/throttle"  //引入自定义 vue.use()后全局可用
import "@/utils/directive/loadMore" //引入自定义指令
// directive(Vue)
import {Carousel,CarouselItem,Image,Table,TableColumn,Divider,Button,Popover,Tag,Message,Select,Option,Card,
  Form,FormItem,Input,Loading,Pagination,InputNumber,Row,Col,DatePicker,Menu,Submenu,MenuItem,Upload
} from "element-ui";

import "@/utils/js/fun" //引入公共方法
import "@/icons" //引入icons

Vue.config.productionTip = false


Vue.use(Carousel);
Vue.use(CarouselItem);
Vue.use(Image);
Vue.use(Table);
Vue.use(TableColumn);
Vue.use(Divider);
Vue.use(Button);
Vue.use(Popover);
Vue.use(Tag);
Vue.use(Select);
Vue.use(Option);
Vue.use(Card)
Vue.use(Form)
Vue.use(FormItem)
Vue.use(Input)
Vue.use(Loading)
Vue.use(Pagination)
Vue.use(InputNumber)
Vue.use(Row)
Vue.use(Col)
Vue.use(DatePicker)
Vue.use(Menu)
Vue.use(Submenu)
Vue.use(MenuItem)
Vue.use(Upload)
Vue.use(throttle)

Vue.prototype.$message = Message;//全局使用this.$message方法 如：this.$message.success("成功设置默认工作组")
Vue.prototype.axios = axios;
Vue.prototype.qs = Qs;

new Vue({
  el: '#app',
  router,
  store,
  components: { App },
  template: '<App/>'
})



